import Clip from './clip';

export default class Results {
  constructor(requestLink) {
    this.requestLink = requestLink;
  }

  static extractData(data) {
    return data.items.map((item) => {
      const {
        id,
        snippet: {
          publishedAt = ' ',
          title = ' ',
          description = ' ',
          thumbnails: {
            medium: { url } = ' ',
          },
          channelTitle = ' ',
        },
        statistics: { viewCount } = { viewCount: ' ' },
      } = item;
      const link = `https://www.youtube.com/watch?v=${id}`;
      return new Clip(title, url, channelTitle, publishedAt, viewCount, description, link);
    });
  }

  async getResults() {
    const responce = await fetch(this.requestLink);
    const data = await responce.json();
    return Results.extractData(data);
  }
}
