export default class Search {
  constructor(keyWords, token) {
    this.key = 'AIzaSyDJ7d9ogh4jPfBhzAevd9_1xP2IhhJ9r_A';
    this.keyWords = keyWords;
    this.token = token;
  }

  async makeRequest() {
    let requestIds;
    if (this.token) {
      requestIds = await fetch(`https://www.googleapis.com/youtube/v3/search?key=${this.key}&type=video&part=snippet&maxResults=15&pageToken=${this.token}&q=${this.keyWords}`);
    } else {
      requestIds = await fetch(`https://www.googleapis.com/youtube/v3/search?key=${this.key}&type=video&part=snippet&maxResults=15&q=${this.keyWords}`);
    }

    const ids = await requestIds.json();
    if (ids.nextPageToken) {
      this.token = ids.nextPageToken;
      // console.log(this.token);
    }
    const idList = ids.items.map(item => item.id.videoId).join(',');
    const requestLink = `https://www.googleapis.com/youtube/v3/videos?key=${this.key}&id=${idList}&part=snippet,statistics`;
    return requestLink;
  }
}
