export default class ClipView {
  constructor(clip) {
    this.clip = clip;
  }

  render() {
    const clipContainer = document.createElement('div');
    clipContainer.className = ('clip_container');

    const linkField = document.createElement('a');
    linkField.className = 'link_field';
    linkField.innerText = `${this.clip.title}`;
    linkField.setAttribute('href', `${this.clip.link}`);
    clipContainer.appendChild(linkField);

    const picContainer = document.createElement('div');
    picContainer.className = 'pic_container';
    clipContainer.appendChild(picContainer);

    const picField = document.createElement('img');
    picField.className = 'pic_field';
    picField.setAttribute('src', `${this.clip.pic}`);
    picField.setAttribute('alt', 'image not found');
    picContainer.appendChild(picField);

    const channelField = document.createElement('div');
    channelField.className = 'channel_field';
    channelField.innerText = `${this.clip.channel}`;
    clipContainer.appendChild(channelField);

    const dateField = document.createElement('div');
    dateField.className = 'date_field';
    dateField.innerText = `${this.clip.date}`;
    clipContainer.appendChild(dateField);

    const viewsField = document.createElement('div');
    viewsField.className = 'views_field';
    viewsField.innerText = `${this.clip.views}`;
    clipContainer.appendChild(viewsField);

    const descriptionField = document.createElement('div');
    descriptionField.className = 'description_field';
    descriptionField.innerText = `${this.clip.description}`;
    clipContainer.appendChild(descriptionField);

    return clipContainer;
  }
}
