import YoutubeView from '../views/startView';
import Search from '../models/search';
import Results from '../models/results';
import ClipView from '../views/clipView';

export default class YoutubeApp {
  constructor() {
    this.link = '';
    this.keyWords = '';
    this.token = '';
  }

  async getResults() {
    const startSearch = new Search(this.keyWords, this.token);
    this.link = await startSearch.makeRequest();
    this.token = await startSearch.token;
    // console.log(this.token);
    const results = new Results(this.link);
    const finalResults = await results.getResults();
    const videoBoxVar = document.querySelector('.video_box');
    finalResults.forEach((item) => {
      const clipVar = new ClipView(item);
      videoBoxVar.appendChild(clipVar.render());
    });
    document.getElementById('button_prev').classList.remove('hidden');
  }

  async start() {
    console.log(this.link);
    const startView = new YoutubeView();
    startView.render();

    const clipWidth = 320;

    // get size
    function getSize() {
      const windowWidth = document.body.clientWidth;
      return Math.min(Math.floor(windowWidth / clipWidth), 4);
    }

    let clipNum = getSize();

    let currentPage = 1;
    const pageNum = document.querySelector('#button_current');
    const videoBox = document.querySelector('.video_box');

    window.addEventListener('resize', () => {
      const pages = Math.floor((currentPage - 1) * clipNum / getSize());
      clipNum = getSize();
      videoBox.scrollLeft = pages * clipNum * clipWidth;
    });

    // videos adding
    const searchBox = document.querySelector('.search__box');

    searchBox.addEventListener('change', (event) => {
      this.keyWords = event.target.value.split(' ').join(',');
      while (videoBox.lastChild) {
        videoBox.removeChild(videoBox.lastChild);
      }

      this.getResults();
      document.querySelector('.buttons_container').classList.remove('hidden');
      pageNum.innerText = '1';
      currentPage = 1;

      // console.log(this.link);
      // console.log(finalResults);
    });

    // scroll/swipe


    function scroll(direction) {
      // clipNum = getSize();
      if (direction === 'right') { // && проверить если ли на стр клипы
        currentPage += 1;
      } else if (direction === 'left' && currentPage > 1) {
        currentPage -= 1;
      }
      videoBox.scrollLeft = clipWidth * clipNum * (currentPage - 1);
      pageNum.innerText = currentPage;
    }

    // подргузка клипов
    // async function loadPages() {
    //   const videoCounter = clipNum * (currentPage - 2);
    //   const allvideos = document.querySelectorAll('.clip_container').length - 5;
    //   if (videoCounter > allvideos && allvideos > 0) {
    //     this.link = await startSearch.makeRequest(this.keyWords, startSearch.token);
    //     console.log(this.link);
    //     const results = new Results(this.link);
    //     const finalResults = await results.getResults();

    //     finalResults.forEach((item) => {
    //       const clipVar = new ClipView(item);
    //       document.querySelector('.video_box').appendChild(clipVar.render());
    //     });
    //   }
    // }

    // document.getElementById('button_next').setAttribute('data-tooltip', '2');

    document.querySelector('.buttons_container').addEventListener('click', (event) => {
      clipNum = getSize();
      if (event.target.getAttribute('id') === 'button_prev') {
        event.target.setAttribute('data-tooltip', `${currentPage - 1}`);
        scroll('left');
      }
      if (event.target.getAttribute('id') === 'button_next') {
        event.target.setAttribute('data-tooltip', `${currentPage + 1}`);
        // подргузка клипов
        const videoCounter = clipNum * (currentPage - 2);
        const allvideos = document.querySelectorAll('.clip_container').length - 5;
        if (videoCounter > allvideos && allvideos > 0) {
          this.getResults();
        }
        scroll('right');
      }
    });

    let isDown = false;
    let startX;
    let currentX;

    videoBox.addEventListener('pointerdown', (event) => {
      isDown = true;
      startX = event.pageX - videoBox.offsetLeft;
      // расстояние от левого верхнего угла до положения курсора
      videoBox.classList.add('grab');
    });

    videoBox.addEventListener('pointermove', (event) => {
      if (isDown) {
        currentX = event.pageX - videoBox.offsetLeft;
        event.preventDefault();
      }
    });

    videoBox.addEventListener('pointerup', () => {
      isDown = false;
      // подргузка клипов
      const videoCounter = clipNum * (currentPage - 2);
      const allvideos = document.querySelectorAll('.clip_container').length - 5;
      if (videoCounter > allvideos && allvideos > 0) {
        this.getResults();
      }
      if (currentX > startX) scroll('left');
      else if (currentX < startX) scroll('right');
      videoBox.classList.remove('grab');
    });

    videoBox.addEventListener('pointerleave', () => {
      isDown = false;
      videoBox.classList.remove('grab');
    });
  }
}
