export default class YoutubeView {
  constructor() {
    this.placeholder = 'Search';
  }

  render() {
    const mainContainer = document.createElement('main'); // ввод переменной, в скобках теги
    mainContainer.className = ('main__container'); // для css присвоение класса
    document.body.appendChild(mainContainer); // добавление пемеренной на стр
    // create search box
    const searchBoxContainer = document.createElement('section');
    searchBoxContainer.className = ('search__box__container');
    mainContainer.appendChild(searchBoxContainer);
    // create input
    const searchBox = document.createElement('input');
    searchBox.className = ('search__box');
    searchBox.setAttribute('type', 'text'); // добавляем тип инпута
    searchBox.setAttribute('placeholder', `${this.placeholder}`);
    searchBoxContainer.appendChild(searchBox);

    // create video container
    const videoContainer = document.createElement('section');
    videoContainer.className = ('video_container');
    mainContainer.appendChild(videoContainer);

    // create video box
    const videoBox = document.createElement('div');
    videoBox.className = ('video_box');
    videoContainer.appendChild(videoBox);

    // create buttons container
    const buttonsContainer = document.createElement('section');
    buttonsContainer.className = ('buttons_container');
    buttonsContainer.classList.add('hidden'); // add class (or toggle or remove)
    mainContainer.appendChild(buttonsContainer);

    // create buttons
    const buttonPrev = document.createElement('div');
    buttonPrev.className = ('buttons');
    buttonPrev.setAttribute('id', 'button_prev');
    buttonPrev.classList.add('hidden');
    buttonPrev.innerText = '<';
    // добавить значение  buttonPrev.setValue('<');
    buttonsContainer.appendChild(buttonPrev);

    const buttonCurrent = document.createElement('div');
    buttonCurrent.className = ('buttons');
    buttonCurrent.innerText = '1';
    buttonCurrent.setAttribute('id', 'button_current');
    buttonsContainer.appendChild(buttonCurrent);

    const buttonNext = document.createElement('div');
    buttonNext.className = ('buttons');
    buttonNext.setAttribute('id', 'button_next');
    buttonNext.innerText = '>';
    buttonsContainer.appendChild(buttonNext);
  }
}
